.responsive_menu{
    margin-right: 30px;
}
.responsive_menu .btn {
    width: 50px;
    height: 100%;
    cursor: pointer;
  }

  .responsive_menu span {
    display: block;
    width: 100%;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
    border-radius: 5px;
    height: 5px;
    background: #C9AB82;
    transition: all .3s;
    position: relative;
  }
  
  span + span {
    margin-top: 10px;
  }
  
  .active span:nth-child(1) {
    -webkit-animation: ease .7s top forwards;
            animation: ease .7s top forwards;
  }
  
  .not-active span:nth-child(1) {
    -webkit-animation: ease .7s top-2 forwards;
            animation: ease .7s top-2 forwards;
  }
  
  .active span:nth-child(2) {
    -webkit-animation: ease .7s scaled forwards;
            animation: ease .7s scaled forwards;
  }
  
  .not-active span:nth-child(2) {
    -webkit-animation: ease .7s scaled-2 forwards;
            animation: ease .7s scaled-2 forwards;
  }
  
  .active span:nth-child(3) {
    -webkit-animation: ease .7s bottom forwards;
            animation: ease .7s bottom forwards;
  }
  
  .not-active span:nth-child(3) {
    -webkit-animation: ease .7s bottom-2 forwards;
            animation: ease .7s bottom-2 forwards;
  }
  
  @-webkit-keyframes top {
    0% {
      top: 0;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    50% {
      top: 10px;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    100% {
      top: 15px;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
    }
  }
  
  @keyframes top {
    0% {
      top: 0;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    50% {
      top: 10px;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    100% {
      top: 15px;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
    }
  }
  
  @-webkit-keyframes top-2 {
    0% {
      top: 22px;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
    }
    50% {
      top: 22px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      top: 0;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }
  
  @keyframes top-2 {
    0% {
      top: 22px;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
    }
    50% {
      top: 22px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      top: 0;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }
  
  @-webkit-keyframes bottom {
    0% {
      bottom: 0;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    50% {
      bottom: 10px;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    100% {
      bottom: 15px;
      -webkit-transform: rotate(135deg);
              transform: rotate(135deg);
    }
  }
  
  @keyframes bottom {
    0% {
      bottom: 0;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    50% {
      bottom: 10px;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    100% {
      bottom: 15px;
      -webkit-transform: rotate(135deg);
              transform: rotate(135deg);
    }
  }
  
  @-webkit-keyframes bottom-2 {
    0% {
      bottom: 22px;
      -webkit-transform: rotate(135deg);
              transform: rotate(135deg);
    }
    50% {
      bottom: 22px;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    100% {
      bottom: 0;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
  }
  
  @keyframes bottom-2 {
    0% {
      bottom: 22px;
      -webkit-transform: rotate(135deg);
              transform: rotate(135deg);
    }
    50% {
      bottom: 22px;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    100% {
      bottom: 0;
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
  }
  
  @-webkit-keyframes scaled {
    50% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  
  @keyframes scaled {
    50% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  
  @-webkit-keyframes scaled-2 {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    50% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @keyframes scaled-2 {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    50% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
