.btn button {
    font-weight: 300;
    margin:50px 25px 25px 40px;
    font-size: 26px;
    padding: 20px 40px;
    color: #C9AB82;
    border: 1px solid #C9AB82;
    position: relative;
    background: transparent;
}
.btn button::before   {
    
    border: 1px solid #ffffff;
    transition: all 300ms ease;
    background: transparent;
}

.btn button:hover:before {
    left: -1px;
    top: -1px;
    border-color: #FFF;
    transition: all 300ms ease;
}

.btn button::before {
    content: "";
    width: calc(100% );
    height: calc(100% );
    border: 1px solid #C9AB82;
    left: 3px;
    top: 3px;
    display: block;
    position: absolute;
    z-index: 0;
    transition: all 300ms ease;
    box-sizing: unset!important;
}
.slick-dots {
